<template>
  <div class="login" v-show="!hasLogin">
      <i class="iconfont">&#xe604;</i>
      <input type="text" @input="input" v-model='inputVal' maxlength="20"
      placeholder="请输入你的登录凭证 英文数字汉字最长20">

      <van-button type="primary" size="large" @click="login" class='click'>
          <div v-show="!ws_login">点此登录</div>
          <div v-show="ws_login">登录中. . .</div>
      </van-button>

  </div>
</template>

<script>
  // vuex
  // import {mapMutations,createNamespacedHelpers} from 'vuex'
  import {createNamespacedHelpers} from 'vuex'
  const login=createNamespacedHelpers('login')
  export default{
    name:"login",
    /* 数据*/
    data(){
      return {
        /* 输入框值*/
        inputVal : '',
        ws_login : false
      }
    },
    /* 方法*/
    methods:{
      // 输入事件
      input(){
        // this.inputVal=this.inputVal.replace(/[^\a-\z\A-\Z0-9\u4E00-\u9FA5]/g,'')
        this.inputVal=this.inputVal.replace(/[^\a-\z\A-\Z0-9\u4E00-\u9FA5]/g,'')
        console.log(this.inputVal,this.inputVal.length)
      },
      // ...mapMutations({
      // 	setTokenLoginCount : 'setTokenLoginCount'
      // }),
      ...login.mapMutations({
            	HasTokenThenFirstHandler : 'HasTokenThenFirstHandler'
            }),
      ...login.mapMutations({
              hasLoginHandler: 'hasLoginHandler'
            }),
      ...login.mapMutations({
              userInfoHandler: 'userInfoHandler'
            }),
      // 登录按钮////////////////////////////////////////////////////////////////////////
      login(){
        if(this.inputVal.length == 0){
          this.$toast("请输入你的凭证")
          return;
        }
        if(!!this.ws_login == true){
          this.$toast("正在登录中...")
          return;
        }
        this.ws_login = true
        var params = {
          /* 账号 */
          user_account : this.inputVal
        }
        this.$api.login.testLogin(params).then(
            res =>{
              console.log(res.data,'login')
              this.ws_login = false
                switch(res.data){
                  case "No_information":
                      this.$toast('没有此id信息');
                      break;
                  default :
                      // 本地储存设置token
                      localStorage.setItem('token', res.data.token)
                      // 设置登录状态为true
                      this.hasLoginHandler(true)
                      //设置第一次进入网页为false
                      this.HasTokenThenFirstHandler(false)
                      this.userInfoHandler(res.data.result)
                      this.$toast('登录成功!!')
                      this.$router.replace({
                        path:'/user'
                      })
                }
            }
        )
      }
    },
    /* 计算*/
    computed:{
     ...login.mapState({
          	hasLogin : state => state.hasLogin
          })
    },
    /* 挂载阶段*/
    mounted() {
        /* 如果用户登录则跳转到用户页*/
        if(this.hasLogin){
          this.$router.replace({
            path:'/user'
          })
        }
    },
    // 监听
    watch:{

      // code: {
      //     handler(newName, oldName) {
      //     var codeReg = /^[\d]{6}$/;
      //     if(!codeReg.test(this.code)){
      //       this.errCode = '验证码是六位的纯数字'
      //     }else{
      //       this.errCode = ''
      //     }
      //     },
      //     // immediate: true,
      //     // deep: true
      //   }
    }
  }
</script>

<style>
 .van-button__text>.van-count-down{
    color: white;
  }
</style>
<style scoped lang="scss">
  .login{
    position: relative;
    @include wh(700px,100%);
    margin: 100px auto 0;
    i{
      position: absolute;
      font-size: 50px;
      color: #969696;
      top: 30px;
      left: 10px;
    }
    input{
      border-radius: 20px;
      @include wh(620px,100px);
      padding-left: 80px;
      outline: none;
      border: 1px solid #c8c8c8;
      background-color: hsla(0,0%,71%,.1);
    }

    .click{
      margin-top: 100px;
    }
  }
</style>



